<template>
  <li class="task-list__item" v-bind:class="{ 'task-list__item--completed': task.completed }">
    <div class="task-list__text-padding scroll-wrapper">
      <div class>
        <span
          class="task-list__text"
          v-bind:class="{ 'task-list__text--completed': task.completed }"
        >{{ task.description }}</span>
      </div>
    </div>

    <button class="task-list__button task-list__button--success" v-on:click="completeTask">
      <span class="icon icon--check"></span>
    </button>
    <button class="task-list__button task-list__button--danger" v-on:click="confirmRemoved">
      <span class="icon icon--delete"></span>
    </button>
  </li>
</template>

<script>
import Noty from "noty";
import "vuejs-noty/dist/vuejs-noty.css";

export default {
  name: "TaskItem",

  props: {
    task: {},
  },

  methods: {
    completeTask() {
      this.$store.dispatch('updateTask', {
        task: this.task,
        onSuccess: () => this.$noty.info(`Task ${this.task.completed? 'Completed' : 'Uncompleted'}.`)
      });
    },

    removeTask() {
      this.$store.dispatch("removeTask", {
        task: this.task,
        onSuccess: () => this.$noty.info('Task Removed.')
      });
    },

    confirmRemoved() {
      let dialog = new Noty({
        type: 'warning',
        layout: 'bottomRight',
        text: "Are you shure you want delete this task?",
        timeout: false,
        closeWith: [],
        modal: true,
        buttons: [
          Noty.button("Ok", "", () => {
            this.removeTask();
            dialog.close();
          }),

          Noty.button("Cancel", "", () => dialog.close()),
        ],
      });

      dialog.show();
    },
  },
};
</script>
