import Vue from 'vue'
import Vuex from 'vuex'
import Noty from "noty"
import axios from "axios";

Vue.use(Vuex)

const onError = error => {
  let dialog = new Noty({
    type: 'error',
    layout: 'center',
    text: `<b>Error code: ${ error.response.data.statusCode }</b><br>An server error has occurred ðŸ˜¢`,
    modal: true
  });

  dialog.show();
}

export default new Vuex.Store({
  state: {
    tasks: [],
    predicate: () => true
  },
  getters: {
    filteredTasks(state) {
      return state.tasks.filter(state.predicate);
    }
  },
  mutations: {
    /**
     * @param { Array<{ description: String, completed: Boolean }> } tasks
     */
    REQUEST_TASKS(state, tasks) {
      tasks.forEach(newTask => {
        let oldTask = state.tasks.find(task => task.id === newTask.id);
        if(oldTask != undefined) {
          oldTask.description = newTask.description;
          oldTask.completed = newTask.completed;
        } else {
          state.tasks.push(newTask);
        }
      });
      // state.tasks = tasks;
    },

    ADD_RESPONSED_TASK(state, task) {
      state.tasks.push(task);
    },

    SET_PREDICATE(state, predicate) {
      state.predicate = predicate;
    },

    REMOVE_TASK(state, task) {
      let index = state.tasks.indexOf(task);
      state.tasks.splice(index, 1);
    },

    /**
     * 
     * @param { { $old: { completed: Boolean }, $new: { completed: Boolean } } } tasks 
     */
    UPDATE_TASK(state, { $old, $new }) {
      let index = state.tasks.indexOf($old);
      state.tasks[index].completed = $new.completed;
    }
  },
  
  actions: {
    getTasks(context) {
      axios.get('https://php-slimframework-taskapi.herokuapp.com/api/v1/tasks')
      .then(response => context.commit('REQUEST_TASKS', response.data.data))
      .catch(onError);
    },

    /**
     * 
     * @param { { task: { description: String }, onSuccess: Function } } payload 
     */
    addTask(context, { task, onSuccess }) {
      axios.post('https://php-slimframework-taskapi.herokuapp.com/api/v1/tasks', {
        description: task.description
      })
      .then(response => {
        context.commit('ADD_RESPONSED_TASK', response.data.data)
        onSuccess();
      })
      .catch(onError);
    },

    /**
     * 
     * @param { { task: any, onSuccess: Function } } payload 
     */
    removeTask(context, { task, onSuccess }) {
      context.commit('REMOVE_TASK', task);
      onSuccess();
    },

    /**
     * 
     * @param { { task: { description: String, completed: Boolean }, onSuccess: Function } } payload 
     */
    updateTask(context, { task, onSuccess }) {
      axios.put(`https://php-slimframework-taskapi.herokuapp.com/api/v1/tasks/${task.id}`, {
        description: task.description,
        completed: !task.completed
      })
      .then(response => {
        context.commit('UPDATE_TASK', {
          $old: task,
          $new: response.data.data
        });
        onSuccess();
      })
      .catch(onError);
    },

    setPredicate(context, predicate) {
      context.commit('SET_PREDICATE', predicate);
    }
  }
})
