<template>
  <form class="task-form">
    <div class="task-form__input-group">
      <input type="text" v-model.trim="task.description" class="task-form__input" />
      <button class="task-form__button task-form__button--transparent" v-on:click.prevent="checkTask">
        <span class="icon task-form-icon icon--plus"></span>
      </button>
    </div>
    <select class="task-form__select" v-model="selectedPredicate">
      <option
        v-for="(predicate, index) in predicates"
        v-bind:key="index"
        v-bind:value="predicate"
      >{{ predicate.text }}</option>
    </select>
  </form>
</template>

<script>
import 'vuejs-noty/dist/vuejs-noty.css';

export default {
  name: "TaskForm",

  data() {
    return {
      task: {
        description: "",
      },

      predicates: [
        { text: "All", value: () => true }, // select all tasks
        { text: "Completed", value: (task) => task.completed }, // select completed tasks
        { text: "Uncompleted", value: (task) => !task.completed }, // select uncompleted tasks
      ],
    };
  },

  computed: {
    selectedPredicate: {
      get() {
        return this.predicates[0];
      },

      set(predicate) {
        this.$store.dispatch('setPredicate', predicate.value);
      }
    },
  },

  methods: {
    clearDescription() {
      this.task.description = '';
    },

    addTask() {
      this.$store.dispatch("addTask", {
        task: this.task,
        onSuccess: () => {
          this.clearDescription();
          this.$noty.success('Task added successfully.');
        }
      });
    },

    checkTask() {
      if(this.task.description === '') {
        this.$noty.error('Description is empty.');
        return;
      }

      if(this.task.description.length >= 128) {
        this.$noty.error('Description should contain less than 128 characters.');
        return;
      }
      
      this.addTask();
    }
  },
};
</script>
