<template>
  <ul class="task-list scroll-wrapper">
    <TaskItem v-for="task in tasks" v-bind:key="task.id" v-bind:task="task"></TaskItem>
  </ul>
</template>

<script>
import TaskItem from "./TaskItem";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    TaskItem,
  },

  name: "TaskLIst",

  computed: {
    ...mapGetters({
      tasks: 'filteredTasks'
    }),
  },

  methods: {
    ...mapActions(["getTasks"])
  },

  mounted() {
    setInterval(() => {
      this.getTasks();
    }, 3000);
  },
};
</script>